import Swal from 'sweetalert2';

const projects = [
  {
    name: "Hire Us – Job Finder Platform",
    description: "Web & Mobile app connecting workers and employers with admin panel.",
    year: 2025
  },
  {
    name: "FOL Clothing",
    description: "E-commerce web & mobile solution with order management and notifications.",
    year: 2024
  },
  {
    name: "CPShareTXT",
    description: "Web platform for sharing notes and files easily with clean UI.",
    year: 2024
  },
  {
    name: "SCU Portfolio",
    description: "Showcasing software projects and applications in a professional portfolio.",
    year: 2025
  },
];

const Projects = () => {
  const showDetails = (project) => {
    Swal.fire({
      title: project.name,
      text: project.description + " (" + project.year + ")",
      icon: 'info',
      confirmButtonText: 'Close'
    });
  };

  return (
    <section id="projects" data-aos="fade-up">
      <h2 className="mb-4 text-center">Projects</h2>
      <div className="row g-4">
        {projects.map((project) => (
          <div key={project.name} className="col-md-6 col-lg-4">
            <div className="card h-100 shadow-sm" onClick={() => showDetails(project)} style={{cursor:"pointer"}}>
              <div className="card-body">
                <h5 className="card-title">{project.name}</h5>
                <p className="card-text">{project.description.substring(0, 60)}...</p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};
export default Projects;
